<template>
  <div>
    <headMenu/>
    <h1>{{msgTitle}}</h1>
    <div class="card container is-fluid">
      <div class="card-content">
        <button v-if="!loading" class="button is-primary" @click="randomGenerate">Generate Jokes</button>
        <button v-else class="button is-primary is-loading">Generate Jokes</button>
        <h2 v-if="!loading">Did you now "{{jokeGenerated}}"</h2>
        <h2 v-else>Loading....</h2>
        </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import headMenu from './HeaderMenu'
import footMenu from './Footer'

export default {
  name: 'ChuckJokes',
  data () {
    return {
      msgTitle: 'Welcome to Chuck Norris Jokes',
      jokeGenerated: '',
      loading: false
    }
  },
  components: {
    headMenu,
    footMenu
  },
  created () {
    this.randomGenerate()
  },
  methods: {
    randomGenerate () {
      // Fetches random joke when the component is created.
      this.loading = true
      axios.get(`https://api.chucknorris.io/jokes/random`).then(response => {
        // JSON responses are automatically parsed.
        this.loading = false
        this.jokeGenerated = response.data.value
      })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  button{
    font-size: 20px;
  }
</style>



