<template>
  <section id="app" class="hero is-warning is-bold is-medium">
    <!-- Start of Navigation Menu -->
    <nav class="navbar is-transparent is-warning" id="headerMenu">
        <div class="container">
          <div class="navbar-brand">
            <a class="navbar-item menuTitle" href="">
              Chuck Norris Facts
            </a>
            <div class="navbar-burger" @click="showNav = !showNav" :class="{ 'is-active': showNav }">
              <span></span>
              <span></span>
              <span></span>
            </div>
          </div>
          <div class="navbar-menu" :class="{ 'is-active': showNav }">
            <div class="navbar-end">
              <a class="navbar-item" href="\">
                Random
              </a>
              <a class="navbar-item" href="">
                Categories
              </a>
              <a class="navbar-item" href="">
                Search
              </a>
            </div>
          </div>
        </div>
      </nav>
      <!-- End of Navigation Menu -->

      <!-- Start of Body -->
      <div class="hero-body has-text-centered" id="body-container">
        <router-view/>
      </div>
      <!-- End of Body -->
  </section>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      showNav: false
    }
  }
}
</script>



