<template>
  <div class="card container is-fluid">
    <div class="card-content">
      <p v-if="!loading" class="title paragraphTextColor"> Did you know “{{jokeGenerated}}” </p>
      <p v-else class="title paragraphTextColor"> Loading... </p>

      <a class="button is-warning is-focused paragraphTextColor" v-if="!loading" @click="randomGenerate">Generate Jokes</a>
      <a v-else class="button is-warning is-loading">Generate Jokes</a>

    </div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'Home',
  data () {
    return {
      jokeGenerated: '',
      loading: false
    }
  },
  created () {
    this.randomGenerate()
  },
  methods: {
    randomGenerate () {
      // Fetches random joke when the component is created.
      this.loading = true
      axios.get(`https://api.chucknorris.io/jokes/random`).then(response => {
        // JSON responses are automatically parsed.
        this.loading = false
        this.jokeGenerated = response.data.value
      })
        .catch(e => {
          this.errors.push(e)
        })
    }
  }
}
</script>



